/*========== Colors ==========*/
.is-typing .acpt-block,
.is-selected .acpt-block {
  padding: 10px; }
  .is-typing .acpt-block .controls,
  .is-selected .acpt-block .controls {
    width: 100%;
    display: block; }

.acpt-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  margin-bottom: 28px;
  width: 100%;
  text-align: left; }
  .acpt-block label {
    display: flex;
    align-items: center;
    white-space: nowrap;
    font-weight: 600;
    flex-shrink: 0; }
  .acpt-block .shortcode {
    border: 1px solid #02c39a;
    display: block;
    width: 100%;
    text-align: center;
    padding: 10px;
    font-size: 14px;
    margin: 10px 0 0 0;
    color: #02c39a;
    border-radius: 3px; }
    .acpt-block .shortcode .icon {
      position: relative;
      top: 4px;
      margin-right: 4px; }
  .acpt-block .controls {
    display: none;
    border: 1px solid #b3bcc0;
    padding: 14px;
    width: 100%;
    border-radius: 3px; }
    .acpt-block .controls .components-base-control + .components-base-control {
      margin-bottom: 0; }
    .acpt-block .controls select,
    .acpt-block .controls input {
      max-width: 100%; }
